
import { defineComponent, ref, watch } from '@vue/composition-api';
import axios from '@/api/axios';
import i18n from 'i18next';
import NavigationBar from '@/views/material-mall/components/NavigationBar.vue';
import SearchBar from './components/Searchbar.vue';

const getMallTemplateConfig = async (companyId) => {
  const result = (await axios.get('/msgcenter/config', {
    params: {
      id: companyId,
      type: 6,
    },
  })).data;
  // console.log('get config=======', result);
  return result || {};
};

const header = defineComponent({

  render() {
    const self = this as InstanceType<typeof header>;
    const { isTopPage, currentPage, mallConfig } = self;
    const { companyId, keyword } = self.$route.query;
    const isQtPath = self.$route.path.startsWith('/qt/') ? '/qt' : '';
    return (
      <NavigationBar class={{ 'profile-nav': true, 'qt-page': isQtPath === '/qt' }}
        height={mallConfig.mode === 'customer' ? '104px' : ''}
        scopedSlots={{
          left: () => {
            return <header class='supplier-page-header flex-vertical-center'>
              <span class="back"
                onclick={
                  () => {
                    if (isTopPage) {
                      self.$router.push(`${isQtPath}/material-mall/home`);
                    } else {
                      self.$router.back();
                    }
                  }
                }>
                <i class='el-icon-arrow-left'></i>
                {isTopPage ? i18n.t('supplier-page.return_to_mart') : i18n.t('material-mall.previous')}
              </span>
              <span>&nbsp;|&nbsp;{currentPage}</span>
            </header>;
          },
          center: () => {
            if (mallConfig.mode === 'customer') {
              return <ul class="profile-nav-list">
                <router-link tag='li' class="profile-nav-item"
                  to={{
                    path: `${isQtPath}/supplier-page/preview`,
                    query: {
                      companyId,
                    },
                  }}>{i18n.t('supplier-backend.supplier_material')}</router-link>
                <router-link tag='li' class="profile-nav-item"
                  to={{
                    path: `${isQtPath}/supplier-page/company-brief`,
                    query: {
                      companyId,
                    },
                  }}>{i18n.t('supplier-backend.supplier_brief')}</router-link>
              </ul>;
            }
            return '';
          },
          right: () => {
            if (mallConfig.mode === 'customer') {
              return <SearchBar class='search-bar flex-center' keyword={keyword as string} />;
            }
            return '';
          },
        }}
      >


      </NavigationBar>
    );
  },

  setup(props, { root }) {
    const currentPage = ref(i18n.t('supplier-page.supplier_info') as string);
    const isTopPage = ref(true);
    watch(() => root!.$route, (newRoute, oldRoute) => {
      if (newRoute.path.endsWith('/supplier-page/preview/homepage')
        || newRoute.path.endsWith('/supplier-page/company-brief')) {
        isTopPage.value = true;
        currentPage.value = i18n.t('supplier-page.supplier_info');
      } else {
        isTopPage.value = false;
        if (newRoute.query.supplierArticleName) {
          currentPage.value = i18n.t('material-mall.material_swatch');
        } else {
          currentPage.value = typeof newRoute.query.pageName === 'string' ? newRoute.query.pageName : i18n.t('supplier-backend.supplier_material');
        }
      }
    }, {
      immediate: true,
    });
    // 获取商家主页配置，为默认主页，则直接展示默认的商家主页，否则展示自定义的主页
    const { companyId } = root.$route.query;
    const mallConfig = ref({});
    (async function init() {
      mallConfig.value = await getMallTemplateConfig(companyId);
    }());

    return {
      currentPage,
      isTopPage,
      mallConfig,
    };
  },
});
export default header;
