<template>
  <div class="navigation"
    :class="{'qt-navigation': $route.path.startsWith('/qt/')}"
    :style="{'height': height}">
    <div class=" flex-space-between navigation-bar"
      :class="{'top-fixed': topFixed, 'hide-background': hideBackground}">
      <div class="nav-left">
        <slot name="left"></slot>
      </div>
      <div class="nav-center">
        <slot name="center"></slot>
      </div>
      <div class="nav-right">
        <slot name="right"></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from '@vue/composition-api';

export default defineComponent({
  name: 'NavigationBar',
  props: {
    height: {
      type: String,
      default: '64px',
    },
    hideBackground: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const topFixed = ref(false);

    window.addEventListener('scroll', () => {
      if (window.scrollY > 0) {
        topFixed.value = true;
      } else {
        topFixed.value = false;
      }
    });

    return {
      topFixed,
    };
  },
});
</script>

<style lang="scss" scoped>
.navigation{
  position: sticky;
  z-index: 10;
  top: 60px;
  width: 100%;
  background-color: transparent;
  &.qt-navigation{
    top: 0;
  }
}

.navigation-bar{
  align-items: stretch;
  user-select: none;
  height: 44px;
  background:#f9f9f9;
  border-bottom: 1px solid #eee;
  transition: all 0.3s linear;
  &.hide-background{
    background: transparent;
    border-bottom: 1px solid transparent;
  }
  // opacity:0.75;
  &.top-fixed{
    border-bottom: 1px solid #eee;
    height: 64px;
    background-color: #fff;
    transition: all 0.1s linear;
    box-shadow:0px 2px 4px 0px rgba(0,0,0,0.1);
    .nav-left,
    .nav-center,
    .nav-right {
      height: 64px;
      margin-top: 0;
      transition: all 0.1s linear;
    }
  }
}

.nav-left,
.nav-center,
.nav-right {
  display: flex;
  align-items: center;
  height: 44px;
  margin-top: 44px;
  transition: all 0.1s linear;
}

.nav-left{
  margin-top: 0;
}

