<template>
  <div>
    <div class="searchbar-wrapper flex-center">
      <el-input
        :placeholder="$t('material-mall.vendor_material_search')"
        prefix-icon="el-icon-search"
        @input="changeKeyword"
        clearable
        :value="keyword">
      </el-input>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watch } from '@vue/composition-api';

const Searchbar = defineComponent({
  props: {
    keyword: {
      type: String,
    },
  },
  setup(props, { root }) {
    function changeKeyword(val) {
      const { $route, $router } = root;
      const isQtPath = root.$route.path.startsWith('/qt/') ? '/qt' : '';
      if ($route.query.keyword) {
        $router.replace({
          path: `${isQtPath}/supplier-page/preview/materials`,
          query: { ...$route.query, keyword: val },
        });
      } else {
        $router.push({
          path: `${isQtPath}/supplier-page/preview/materials`,
          query: { ...$route.query, keyword: val },
        });
      }
    }
    return {
      changeKeyword,
    };
  },
});

export default Searchbar;
</script>

